module.exports = "export function init () {\n  const people   = [\n    {name: 'Wes', year: 1988},\n    {name: 'Kait', year: 1986},\n    {name: 'Irv', year: 1970},\n    {name: 'Lux', year: 2015}\n  ]\n  const comments = [\n    {text: 'Love this!', id: 523423},\n    {text: 'Super good', id: 823423},\n    {text: 'You are the best', id: 2039842},\n    {text: 'Ramen is my fav food ever', id: 123523},\n    {text: 'Nice Nice Nice!', id: 542328}\n  ]\n\n  const cardio = {\n    // is at least one person 19 or older?\n    thereIsAdult: people.some(person => (new Date()).getFullYear() - person.year >= 19),\n    // is everyone 19 or older?\n    isEveryAdult: people.every(person => (new Date()).getFullYear() - person.year >= 19),\n    // Find is like filter, but instead returns just the one you are looking for\n    comment:      comments.find(({id}) => id === 823423),\n    index:        comments.findIndex(({id}) => id === 823423),\n\n    // delete the comment with the ID of 823423\n    delete () {\n      return [\n        ...comments.slice(0, this.index),\n        ...comments.slice(this.index + 1)\n      ]\n    }\n  }\n\n  console.log(cardio)\n  console.table(cardio.delete())\n\n}\n"